import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home/Home.vue'
import Chat from '@/pages/Chat/Chat.vue'
import Mine from '@/pages/Mine/Mine.vue'
import Search from '@/pages/Search/Search.vue'
import Recommend from '@/pages/Recommend/Recommend.vue'

//home子组件
import Box from '@/pages/Home/Child/Box'
import Dress from '@/pages/Home/Child/Dress'
import Ele from '@/pages/Home/Child/Ele'
import Man from '@/pages/Home/Child/Man'
import General from '@/pages/Home/Child/General'
import Food from '@/pages/Home/Child/Food'
import Mbaby from '@/pages/Home/Child/Mbaby'
import Shirt from '@/pages/Home/Child/Shirt'
import Hot from '@/pages/Home/Child/Hot/Hot'

Vue.use(Router);//全局声明

//输出路由对象
export default new Router({
	//配置一级路由
	routes: [
		{
			path: '/home',
			component: Home,
			children:[
				{path: 'hot',component: Hot},
				{path: 'dress',component: Dress},
				{path: 'ele',component: Ele},
				{path: 'food',component: Food},
				{path: 'general',component: General},
				{path: 'man',component: Man},
				{path: 'mbaby',component: Mbaby},
				{path: 'shirt',component: Shirt},
				{path: 'box',component: Box},
				{path: '/home',redirect:'/home/hot'},
			]
		},
		{
			path: '/chat',
			component: Chat
		},
		{
			path: '/mine',
			component: Mine
		},
		{
			path: '/search',
			component: Search
		},
		{
			path: '/recommend',
			component: Recommend
		},
		{
			path: '/',
			redirect:'/home'
		},
	]
})



