<template>
      <div class = 'bottom-tab'>
            <span class = 'btm-tab-item' @click="switchTo('/home')">
                  <img 
                  :src="$route.path.includes('/home')//判断路径是否这一段内容
                  ? tabBarImgArr[0].selected 
                  : tabBarImgArr[0].normal" alt="">
                  <span :class="{on:$route.path.includes('/home')}">首页</span>
            </span>
            <span class = 'btm-tab-item' @click="switchTo('/recommend')">
                  <img :src="$route.path.includes('/recommend')
                  ? tabBarImgArr[1].selected 
                  : tabBarImgArr[1].normal" alt="">
                  <span :class="{on:$route.path.includes('/recommend') }">推荐</span>
            </span>
            <span class = 'btm-tab-item' @click="switchTo('/search')">
                  <img :src="$route.path.includes('/search')
                  ? tabBarImgArr[2].selected 
                  : tabBarImgArr[2].normal" alt="">
                  <span :class="{on:$route.path.includes('/search')}">搜索</span>
            </span>
            <span class = 'btm-tab-item' @click="switchTo('/chat')">
                  <img :src="$route.path.includes('/chat')
                  ? tabBarImgArr[3].selected 
                  : tabBarImgArr[3].normal" alt="">
                  <span :class="{on:$route.path.includes('/chat')}">聊天</span>
            </span>
            <span class = 'btm-tab-item' @click="switchTo('/mine')">
                  <img :src="$route.path.includes('/mine')
                  ? tabBarImgArr[4].selected 
                  : tabBarImgArr[4].normal" alt="">
                  <span :class="{on:$route.path.includes('/mine')}">个人中心</span>
            </span>
      </div>
</template>

<script>
export default {
      name: 'tabbar',
      data() { 
            return {
                  tabBarImgArr:[
                        {normal:require('../../../static/img/icon_home.png'),
                        selected:require('../../../static/img/icon_home_selected.png')},
                        {normal:require('../../../static/img/icon_intro.png'),
                        selected:require('../../../static/img/icon_intro_selected.png')},
                        {normal:require('../../../static/img/icon_search.png'),
                        selected:require('../../../static/img/icon_search_selected.png')},
                        {normal:require('../../../static/img/icon_chat.png'),
                        selected:require('../../../static/img/icon_chat_selected.png')},
                        {normal:require('../../../static/img/icon_mine.png'),
                        selected:require('../../../static/img/icon_mine_selected.png')},
                  ]
                  
            }
      },
      methods:{
            switchTo(p){
                  this.$router  .replace(p);
            }
      }
}
</script>

<!-- style  ref 风格参考 -->
<!-- 
      flex  属性是flex-grow，flex-shrink和flex-basis的简写 
            flex-basis属性：定义在分配多余空间之前，项目占据的主轴空间。
                  flex-basis:  <auto或者px>;
                  注意设置的flex-basis是分配多余空间之前项目占据的主轴空间，
                  如果空间不足则默认情况下该项目也会缩小。
            flex-shrink属性：定义项目的缩小比例
                  默认值都为1，即如果空间不足将等比例缩小。
                  如果有一个项目的值为0，其他项目为1，当空间不足时，该项目不缩小。
                  负值对该属性无效，容器不应该设置flex-wrap。
                  flex-shrink: <非负整数>;
                  如果一个项目设置flex-shrink为0；
                  而其他项目都为1，则空间不足时，该项目不缩小。
                  设置flex-shrink为0的项目不缩小
                  如果所有项目都为0，则当空间不足时，项目撑破容器而溢出。
            flex-grow属性：定义项目的放大比例
                  默认值为0，即如果空间有剩余，也不放大。
                  可以是小数，按比例占据剩余空间。
                  flex-grow: <数字>;
                  若所有项目的flex-grow的数值都相同，则等分剩余空间
      flex-direction  column 决定主轴的方向（即项目的排列方向）
      align-items  center          定义在交叉轴上的对齐方式
      justify-content  center        定义项目在主轴上的对齐方式。
-->
<style lang="stylus" scoped ref="stylesheet/stylus">
      .bottom-tab
            width 100%
            height 50px
            background-color #fff
            position fixed
            left 0
            bottom 0
            display flex
            z-index 999
            .btm-tab-item
                  display flex
                  flex 1
                  flex-direction  column
                  align-items  center
                  justify-content  center
                  font-size  12px
                  color #666
                  img
                        width  30%
                        margin-bottom  4px
                  .on
                        color #e12a1f
            
</style>


