import{
      getHomeCasual
} from '../api'
import{
      HOME_CASUAL
}from './mutations-types'
export default{
      // 获取首页轮播图
      async reqHomeCasual({commit}){
            // 这本身是一个异步请求
            const result = await getHomeCasual();
            // await  是等待获取完成 后 再往下执行  就变成了同步
            commit(HOME_CASUAL, {homecasual:result.message.data})
      }
}


