<template>
      <div class = "hot-nav">
            <!-- 内容 -->
            <div class = "hot-nav-content">
                  <div class = "nav-content-inner" :style="innerNavStyle">
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                        <a class="inner-item">
                              <img src="./../../imgs/nav/nav_icon01.png" alt="">
                              <span>限时秒杀</span>
                        </a>
                  </div>
            </div>
            <!-- 进度条 -->
            <div class="hot-nav-bottom">
                  <div class="hot-nav-bottom-inner" :style="innerBarStyle"></div>
            </div>
      </div>
</template>

<script>
export default {
      data() { 
            return {
                  //屏幕宽度
                  screenW:window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
                  // 滚动内容宽度,
                  scrollContenWidth:720,
                  //滚动背景的长度
                  bgBarW:100,
                  //4.滚动条长度
                  barXWidth:0,
                  //起点
                  startX:0,
                  // 结束点
                  endFlag:0,
                  //移动距离
                  barMoveWidth:0,
                  //滚动条的左面
                  scrollMarginL:0,
                  //滚动条的左面
                  navMarginL:0,
                  //动画效果的秒数
                  seconds:0
            }
      },
      mounted(){
            this.getBottomBarWidth();
            this.bindEvent();
      },
      computed:{
            innerBarStyle(){
                  return {
                        width:`${this.barXWidth}px`,
                        left:`${this.barMoveWidth}px`,
                        marginLeft:`${this.scrollMarginL}px`,
                        marginReft:`${this.scrollMarginR}px`,
                        transition: `margin ${this.seconds}s`
                  }
            },
            innerNavStyle(){
                  return {
                        marginLeft:`${this.navMarginL}px`,
                        marginReft:`${this.navMarginR}px`,
                        transition: `margin ${this.seconds}s`
                  }
            }
      },
      methods:{
            getBottomBarWidth(){ 
                  const {screenW, scrollContenWidth, bgBarW} = this;
                  this.barXWidth = (screenW / scrollContenWidth) * bgBarW;  
                  // console.log(this.barXWidth);
            },
            //移动端时间监听
            bindEvent(){
                  // touchstart   touchmove touchend  一定小写 
                  this.$el.addEventListener('touchstart', this.handleTouchStart, false)
                  this.$el.addEventListener('touchmove', this.handleTouchMove, false)
                  this.$el.addEventListener('touchend', this.handleTouchEnd, false)
            },
            //开始触摸
            handleTouchStart(event){
                  // console.log('开始触摸');
                  //  获取第一个触摸点
                  let touch = event.touches[0];
                  //求出起始点
                  this.startX = Number(touch.pageX);
                  this.seconds= 0;
                  this.navMarginL = 0;
                  this.scrollMarginL = 0;
            },
            //开始移动
            handleTouchMove(event){
                  // console.log('开始移动');
                   //  获取第一个触摸点
                  let touch = event.touches[0];
                  // 求出移动距离
                  let moveW = Number(touch.pageX) - this.startX;
                  this.barMoveWidth = (-moveW / this.scrollContenWidth * this.bgBarW)+this.endFlag;

                  if(this.barMoveWidth >= this.bgBarW-this.barXWidth){
                        // this.navMarginL = -this.barMoveWidth - this.screenW;
                        // this.scrollMarginL = this.barMoveWidth/this.scrollContenWidth * this.bgBarW
                        // console.log(-this.barMoveWidth);
                        this.barMoveWidth = this.bgBarW-this.barXWidth;
                  }else if(this.barMoveWidth < 0){
                        this.navMarginL = -this.barMoveWidth;
                        this.scrollMarginL = this.barMoveWidth/this.scrollContenWidth * this.bgBarW;
                        this.barMoveWidth = 0;
                  }
                  // else if(this.barMoveWidth >= 0){
                  //       this.barMoveWidth = (-moveW / this.scrollContenWidth * this.bgBarW)+this.endFlag;
                  // }


            },
            //结束移动
            handleTouchEnd(event){
                  // console.log('结束移动');
                  this.endFlag = this.barMoveWidth;
                  if(this.marginLeft < 0 || this.scrollMarginL < 0){
                        this.navMarginL = 0;
                        this.scrollMarginL = 0;
                        // this.barMoveWidth = 0;
                        this.seconds = 1;
                  }else if(this.marginLeft > 0 || this.scrollMarginL > 0){
                        // this.barMoveWidth = this.bgBarW-this.barXWidth;
                        this.navMarginL = 0;
                        this.scrollMarginL = 0;
                        this.seconds = 1;
                  }
            }
      }
}
</script>

<!-- style  ref 风格参考   首页板块1-->
<style lang="stylus" scoped ref="stylesheet/stylus">
      .hot-nav
            width 100%
            height 180px
            position relative
            background-color #ffffff
            padding-bottom  10px
            .hot-nav-content
                  width 100%
                  overflow-x scroll
                  .nav-content-inner
                        width 720px
                        height 180px
                        display flex
                        flex-wrap wrap
                        margin-left 0
                        margin-right 0
                        transition margin 0s
                        .inner-item
                              width 90px
                              height 90px
                              display flex
                              flex-direction  column
                              justify-content center 
                              align-items  center
                              font-size 14px
                              color #666
                              img 
                                    width 45%
                                    margin-bottom 5px;
            .hot-nav-content::-webkit-scrollbar
                  display none
            .hot-nav-bottom
                  width 100px
                  height 2px
                  background-color #ccc
                  position absolute
                  left 50%
                  margin-left -50px
                  bottom 8px
                  overflow hidden
                  .hot-nav-bottom-inner
                        position absolute
                        left 0
                        margin-left 0
                        margin-right 0
                        height 100%
                        background-color orangered
                        width 0
                        transition margin 0s
                  

</style>


