<template>
      <div class = "hot">
            <!--1.轮播图-->
            <div class="swiper-container" >
                  <div class="swiper-wrapper">
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s1.png" alt="" width="100%">
                        </div>
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s2.png" alt="" width="100%">
                        </div>
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s3.png" alt="" width="100%">
                        </div>
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s4.png" alt="" width="100%">
                        </div>
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s5.png" alt="" width="100%">
                        </div>
                        <div class="swiper-slide">
                              <img src="./../../imgs/rowing/s6.png" alt="" width="100%">
                        </div>
                  </div>
                  <!-- 如果需要分页器 -->
                  <div class="swiper-pagination"></div>
            </div>
            <!-- 2.中间导航 -->
            <hot-nav />
            <!-- 广告位 -->
            <div class= "hot-ad">
                  <img src="./../../imgs/hot_ad/home_ad.gif">
            </div>
            <!-- 商品栏 -->
            <hot-shop-list></hot-shop-list>
      </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
import{
      mapState
}from 'vuex'

import HotNav from './HotNav'
import HotShopList from './HotShopList'
export default {
      name: 'hot',
      components:{
            HotNav,
            HotShopList
      },
      data() { 
            return {

            }
      },
      computed:{
            ...mapState(['homecasual'])
      },
      mounted(){
            //请求轮播图
            this.$store.dispatch('reqHomeCasual');//请求完后才会执行下一步


            // //创建swiper实例  所以在这里更改方法
            // new Swiper ('.swiper-container',{
            //       autoplay:true,
            //       loop:true,
            //       //分页器
            //       pagination:{
            //             el:'.swiper-pagination',
            //       }
            // })
      },
      watch: {//通过监听请就完成后  执行
            homecasual(){//homecasual是引入的一个数据变量    这里代表监听 他的变化   
                  this.$nextTick(() => {
                        //创建swiper实例
                        new Swiper ('.swiper-container',{
                              autoplay:true,
                              loop:true,
                              //分页器
                              pagination:{
                                    el:'.swiper-pagination',
                              }
                        })
                  });
            }
      }
}
</script>

<!-- style  ref 风格参考 -->
<style lang="stylus" scoped ref="stylesheet/stylus">
      .hot
            width 100%
            height 100%
            padding-top 43px
            background-color #f5f5f5
            .hot-ad
                  background-color #ffffff
                  margin  8px 0
                  padding 5px
                  img
                        width 100%
</style>


