<template>
      <div  class="home">
            <ly-tab v-model="selectedId"
            :items="items"
            :options="options"
            @change="ch"
            class = "fix"
            ></ly-tab>
            <router-view></router-view>
      </div>
</template>

<script>
export default {
      name: 'home',
      data() { 
            return {
                  selectedId:0,//选中的id
                  items:[
                        {label: '热门',p:'hot'},
                        {label: '服饰',p:'dress'},
                        {label: '鞋包',p:'box'},
                        {label: '母婴',p:'mbaby'},
                        {label: '百货',p:'general'},
                        {label: '食品',p:'food'},
                        {label: '内衣',p:'shirt'},
                        {label: '男装',p:'man'},
                        {label: '电器',p:'ele'}
                  ],
                  options: {
                        activeColor: '#e9232c', // 设置选中的颜色
                  }
            }
      },
      methods:{
            ch(item,index){
                  this.$router.replace('/home/' + item.p);
                  
            }
      }
}
</script>

<!-- style  ref 风格参考 -->
<style lang="stylus" scoped ref="stylesheet/stylus">
      .home
            background-color #00FF7F
            .fix
                  position fixed
                  left 0
                  top 0
                  z-index 998
</style>


